@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.header {
  background: #C03F6F;
  text-align: center;
  height: 200px;
  padding-top: 40px;
  color: white;
  font-family: 'Roboto', sans-serif;
}

.header-bottom{
  background: #3FC090;
  height: 10px;
}
.search-bar {
  margin-top: 50px;
  text-align: center;
}
.table-employee {
  margin-top: 50px;
}

.table {
  width: 70% !important; 
  margin: auto;
  text-align: center;
}

th:nth-child(1) {
  width: 5%;
}

th:nth-child(2) {
  width: 25%;
}
th:nth-child(3) {
  width: 20%;
}
th:nth-child(4) {
  width: 30%;
}
th:nth-child(5) {
  width: 20%;
}

.pointer {
  cursor: pointer;
}

.pointer:hover > .icon {
  visibility: visible;
}

.icon {
  visibility: hidden;
}

.rotate-false {
  color: red;
}

.rotate-true {
  color: blue;
  transform: rotate(180deg);
}


